.categoryselect{
    height: 100%;
    padding: 5px;
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    color: #555;
    outline: none;
    margin-left: 3%;
    margin-bottom: 1rem;
}

.table{
    width: 90%;
}

.tableId{
    width: 10%;
}
.tableImage{
    width: 10%;
}
th {
    background-color: #04AA6D;
    color: white;
}
tr:nth-child(even) {background-color: #f2f2f2;}
thead tr th{
    width: 50px;
}
tr:hover{
    background-color: #8a8a8a;
}
tr td {
    padding: 5px;
}
.header{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 80%;
    height: 60px;
    background-color: #220646;
    color: #fff;
    padding: 10px;
    text-align: center;
}
.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}